.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
  }
  
  .text {
    margin-bottom: 20px;
  }
  
  .input {
    padding: 8px;
    margin-right: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .create-task-button,
  .mark-as-done-button {
    padding: 8px 16px;
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .create-task-button:active,
  .mark-as-done-button:active {
    background-color: #fff;
    color: #333;
  }
  
  .mark-as-done-button[disabled] {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  .button button {
    padding: 8px 16px;
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .tasks {
    margin-top: 20px;
  }
  
  .task {
    background-color: #e0e0e0;
    border: 1px solid #999;
    border-radius: 8px;
    padding: 10px;
    margin-bottom: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .task.done {
    background-color: #a0a0a0;
  }
  
  .task-buttons {
    margin-top: 8px;
  }
  
  .task-buttons button {
    padding: 6px 12px;
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .task-buttons button[disabled] {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  .task-buttons button.done {
    background-color: #333;
  }
  